import PizZip from 'pizzip'
import docxtemplater from 'docxtemplater'
import JSZipUtils from 'jszip-utils'
import { saveAs } from 'file-saver'

/**
 * base64ArrayBuffer
 * @param {Object} dataURL base64
 */
function base64DataURLToArrayBuffer(dataURL) {
  const base64Regex = /^data:image\/(png|jpg|jpeg|svg|svg\+xml);base64,/
  if (!base64Regex.test(dataURL)) {
    return false
  }
  const stringBase64 = dataURL.replace(base64Regex, '')
  let binaryString
  if (typeof window !== 'undefined') {
    binaryString = window.atob(stringBase64)
  } else {
    binaryString = Buffer.alloc(stringBase64, 'base64').toString('binary')
  }
  const len = binaryString.length
  const bytes = new Uint8Array(len)
  for (let i = 0; i < len; i++) {
    const ascii = binaryString.charCodeAt(i)
    bytes[i] = ascii
  }
  return bytes.buffer
}

/**
 * word,
 * @param {Object} tempDocxPath 
 * @param {Object} wordData 
 * @param {Object} fileName 
 * @param {Object} imgSize 
 */
export const exportWord = (tempDocxPath, wordData, fileName, imgSize) => {
  // 
  var ImageModule = require('docxtemplater-image-module-free')

  const expressions = require('angular-expressions')
  console.log(JSZipUtils)
  // 
  JSZipUtils.getBinaryContent(tempDocxPath, function (error, content) {
    if (error) {
      throw error
    }
    expressions.filters.size = function (input, width, height) {
      return {
        data: input,
        size: [width, height]
      }
    }

    // function angularParser (tag) {
    //   const expr = expressions.compile(tag.replace(//g, '''))
    //   return {
    //     get (scope) {
    //       return expr(scope)
    //     },
    //   }
    // }

    // 
    let opts = {}

    opts = {
      // 
      centered: false
    }

    opts.getImage = (chartId) => {
      // console.log(chartId)//base64
      // base64ArrayBuffer
      return base64DataURLToArrayBuffer(chartId)
    }

    opts.getSize = function (img, tagValue, tagName) {
      // console.log(img)//ArrayBuffer
      // console.log(tagValue)//base64
      // console.log(tagName)//wordData
      // 
      if (imgSize.hasOwnProperty(tagName)) {
        return imgSize[tagName]
      } else {
        return [600, 350]
      }
    }

    // PizZip
    let zip = new PizZip(content)
    // docxtemplater
    let doc = new docxtemplater()
    doc.attachModule(new ImageModule(opts))
    doc.loadZip(zip)

    doc.setData(wordData)

    try {
      // 
      doc.render()
    } catch (error) {
      // 
      let e = {
        message: error.message,
        name: error.name,
        stack: error.stack,
        properties: error.properties
      }
      console.log(JSON.stringify({
        error: e
      }))
      throw error
    }

    // docxtemplaterzip
    let out = doc.getZip().generate({
      type: 'blob',
      mimeType: 'application/vnd.openxmlformats-officedocument.wordprocessingml.document'
    })
    // 
    saveAs(out, fileName)
  })
}
