<template>
  <div>
      <el-main>
        <div style="margin-bottom: 30px">
          <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>
        <div>
          <el-carousel :interval="4000" type="card" height="300px">
            <el-carousel-item v-for="item in imgList" :key="item.id">
              <el-row :gutter="20">
                <el-col :span="6" offset="1"><div class="grid-content bg-purple"><img class="medium" :src="item.idView"></div></el-col>
                <el-col :span="6" offset="6"><div class="grid-content bg-purple">
                  <h1 style="margin-top:15px">{{item.name}}</h1>
                  <p style="margin-top: 20px;white-space:pre-wrap">{{item.description}}</p>
                </div></el-col>
              </el-row>
            </el-carousel-item>
          </el-carousel>
        </div>
        <div style="margin-bottom: 30px">
          <el-breadcrumb separator="/">
            <el-breadcrumb-item></el-breadcrumb-item>
          </el-breadcrumb>
        </div>

        <div style="margin: 10px 0">
          <el-button type="primary" @click="handleAdd"><i class="el-icon-circle-plus-outline" style="margin-left: 2px"></i></el-button>

        </div>
        <el-table :data="tableData" border stripe height="500px">
          <!--          : returnv-bind:-->
          <el-table-column prop="id" label="ID" width="80">
          </el-table-column>
          <el-table-column prop="name" label="" width="300">
          </el-table-column>
          <el-table-column prop="location" label="" width="300">
          </el-table-column>
          <el-table-column prop="fromYuanqu" label="" width="120">
          </el-table-column>
          <el-table-column prop='modelimg' label="" width="120">
            <template slot-scope="scope">
              <img slot="reference" :src="getImgUrl(scope.row.modelimg)" style="width: 50px;height: 50px">
            </template>
          </el-table-column>
          <el-table-column prop="description" label="" width="400">
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button type="success" @click="handleEdit(scope.row)"><i class="el-icon-edit" style="margin-left: 2px"></i></el-button>
              <el-popconfirm
                  confirm-button-text=''
                  cancel-button-text=''
                  icon="el-icon-info"
                  icon-color="red"
                  title=""
                  @confirm="del(scope.row.id)"
              >
              <el-button type="danger" slot="reference"><i class="el-icon-remove-outline" style="margin-left: 2px"></i></el-button>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
        <div style="padding: 10px 0">
          <el-pagination @size-change="sizeChange" @current-change="currentChange"
                         :current-page="page" :page-size="size" :page-sizes="pageSizes"
                         layout="total, sizes, prev, pager, next, jumper" :total="total">
          </el-pagination>
        </div>
        <el-dialog title="" :visible.sync="dialogFormVisible">
          <el-form label-width="100px" size="small">
            <el-form-item label="ID" :label-width="formLabelWidth">
              <el-input v-model="form.id" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.name" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.location" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-select v-model="form.fromYuanqu" placeholder="" >
                <el-option
                    v-for="item in yuanquoptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-select v-model="form.modelimg" placeholder="" >
                <el-option
                    v-for="item in modeloptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.description" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary" @click="save"> </el-button>
          </div>
        </el-dialog>
      </el-main>
  </div>
</template>

<script>
import request from "@/utils/request";
export default {
  name: "showdatabase",

  data() {
    return {
      page: 1, //
      size: 10, //
      total: 0, //
      pageSizes: [3, 5, 10, 20, 50, 100, 200, 300, 400, 500, 1000], //
      tableData: [],
      imgurl: require('/src/assets/logo.png'),
      ALLData:[],
      form:{},
      collapseBtnClass: 'el-icon-s-fold',
      isCollapse: false,
      dialogFormVisible: false,
      sideWidth: 200,
      logoTextShow: true,
      imgList: [
        {id:0,idView:require('/src/assets/factories_models/1.jpg'),name:"1",description:":\r\n\r\n..."},
        {id:1,idView:require('/src/assets/factories_models/2.jpg'),name:"2",description:":\r\n\r\n..."},
        {id:2,idView:require('/src/assets/factories_models/3.jpg'),name:"3",description:":\r\n\r\n..."},
        {id:3,idView:require('/src/assets/factories_models/4.jpg'),name:"4",description:":\r\n\r\n..."},
        {id:4,idView:require('/src/assets/factories_models/5.jpg'),name:"5",description:":\r\n\r\n..."},
        {id:5,idView:require('/src/assets/factories_models/6.jpg'),name:"6",description:":\r\n\r\n..."}
      ],
      yuanquoptions:[{
        value: '1',
        label: '1'
      },{
        value: '2',
        label: '2'
      },{
        value: '3',
        label: '3'
      },{
        value: '4',
        label: '4'
      },{
        value: '5',
        label: '5'
      },{
        value: '6',
        label: '6'
      },{
        value: '7',
        label: '7'
      },{
        value: '8',
        label: '8'
      },],
      modeloptions: [{
        value: 'assets/factories_models/1.jpg',
        label: '1'
      }, {
        value: 'assets/factories_models/2.jpg',
        label: '2'
      }, {
        value: 'assets/factories_models/3.jpg',
        label: '3'
      }, {
        value: 'assets/factories_models/4.jpg',
        label: '4'
      }, {
        value: 'assets/factories_models/5.jpg',
        label: '5'
      },{
        value: 'assets/factories_models/6.jpg',
        label: '6'
      },],
      value: ''
    }
  },

  created() {
    this.$http.get("http://localhost:8088/yuanqu").then((response) => {//createdthis
      console.log(response.data)
      this.ALLData = response.data
      this.getTabelData()
    })
  },
  methods:{
    getTabelData() {
      //allData
      this.tableData = this.ALLData.slice(
          (this.page - 1) * this.size,
          this.page * this.size
      );
      this.total=this.ALLData.length
    },
    currentChange(val) {
      console.log("", val);
      this.page = val;
      this.getTabelData();
    },
    getImgUrl(src) {
      return require("@/" + src);
    },
    //sizesize
    sizeChange(val) {
      console.log("", val);
      this.size = val;
      this.page = 1;
      this.getTabelData();
    },
    handleAdd()
    {
      this.dialogFormVisible = true
      this.form = {}
    },
    del(id){
      request.delete("http://localhost:8088/yuanqu/"+id).then(res=> {
        if(res){
          this.$message.success("")
          this.$router.go(0)
          this.load()
        }else{
          this.$message.error("")
        }
      })
    },
    handleEdit(row){
      this.form=row
      this.dialogFormVisible=true
    },
    save(){
      request.post("http://localhost:8088/insertyuanqu",this.form).then(res =>{
        if(res){
          this.$message.success("")
          this.$router.go(0)
          this.dialogFormVisible=false
        }else{
          this.$message.error("")
        }
      })
    },
  }
}
</script>

<style scoped>
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n+1) {
  background-color: #d3dce6;
}
</style>