<template>
  <div>
    <el-row>
      <el-col :span="12" class="no-print">
        <div class="grid-content bg-purple1">
          <div style="text-align: center;color: #F1F2F7" class="mains"></div>
          <div class="leftButton_top"></div>
          <div class="leftButton">
            <el-row>
              <el-col span="1" offset="0" v-for="(item, index) in titleList1" style="margin-right: 123px">
                <el-tooltip class="item" effect="dark" :content=item.action placement="top-start">
                  <el-button round @click="liClick(item.name)" :key="index" size="medium">{{ item.name }}</el-button>
                </el-tooltip>
              </el-col>
            </el-row>
          </div>
          <div class="leftButton" style="margin-top: 10px">
            <el-row>
              <el-col span="1" offset="0" v-for="(item, index) in titleList2" style="margin-right: 123px">
                <el-tooltip class="item" effect="dark" :content=item.action placement="top-start">
                  <el-button round @click="liClick(item.name)" :key="index" size="medium">{{ item.name }}</el-button>
                </el-tooltip>
              </el-col>
            </el-row>
          </div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="grid-content bg-purple-light">
          <div style="text-align: center" class="no-print mains"></div>
          <div class="operation">
            <span>:
              <select v-model="selectedS">
                <option
                    v-for="option in optionsD"
                    v-bind:value="option.text"
                >
                  {{ option.text }}
                </option>
              </select>
            </span>
            <span
            >:
              <select v-model="selectedOne">
                <option
                    v-for="option in options_list"
                    v-bind:value="option.text"
                >
                  {{ option.text }}
                </option>
              </select></span
            >
          </div>
          <div class="operations">
            <span
            >:
              <select v-model="selectedTwo">
                <option
                    v-for="option in optionsTwo_list"
                    v-bind:value="option.text"
                >
                  {{ option.text }}
                </option>
              </select> </span
            ><span
          >:
              <select v-model="selected">
                <option v-for="option in options" v-bind:value="option.text">
                  {{ option.text }}
                </option>
              </select>
            </span>
          </div>
          <div id="printDown">
            <el-table :data="tableData" stripe style="width: 100%">
              <el-table-column type="index" label="" width="180">
              </el-table-column>
              <el-table-column prop="actionItem" label="" width="640">
              </el-table-column>
            </el-table>
          </div>

          <el-row class="no-print">
            <el-button type="primary" @click="cancelPrve"></el-button>
            <el-button type="primary" @click="exportWordTest"></el-button>
            <!-- <el-button type="primary" @click="exportWord"></el-button> -->
            <!-- <el-button type="primary" @click="pdfDown"></el-button> -->
            <el-button type="primary" @click="dialogVisible = true"></el-button>
            <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%"
                :before-close="handleClose">
              <span></span>
              <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false"> </el-button>
              <el-button type="primary" @click="dialogVisible = false"> </el-button>
              </span>
            </el-dialog>
          </el-row>
        </div>
      </el-col
      >
    </el-row>
  </div>
</template>

<script>
import {exportWord} from '@/plugins/export'

export default {
  name: "make_oplist",
  data() {
    return {
      list: [],
      dialogVisible: false,
      imgurl: require("/src/assets/btn.svg"),
      selected: '', //select
      options: [
        {text: '', value: 'A'},
        {text: '', value: 'B'}
      ],
      selectedOne: '', //select
      options_list: [
        {text: '', value: 'A'},
        {text: '', value: 'B'}
      ],
      selectedTwo: '', //select
      optionsTwo_list: [
        {text: '', value: 'A'},
        {text: '', value: 'B'}
      ],
      selectedS: '10kV***', //select
      optionsD: [
        {text: '10kV***', value: 'A'},
        {text: '10kVXX***', value: 'B'}
      ],
      titleList1: [
        {name: 'A', action: " 10kV I102"},
        {name: 'B', action: " I102"},
        {name: 'C', action: " 10kV I102"},
        {name: 'D', action: " 10kV I002"},
      ],
      titleList2: [
        {name: 'E', action: " 10kV I002"},
        {name: 'F', action: " 10kV I102002"},
        {name: 'G', action: " 10kV I102"},
        {name: 'H', action: " 10kV I102"},
      ],
      tableData: [{actionItem: ' 1518 ', index: ''}],
      value: ''
    }
  },

  methods: {
    handleClose(done) {
      this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
    },
    liClick(e) {
      if (e == 'E') {
        this.tableData.push({actionItem: ' 10kV I002'})
      } else if (e == 'F') {
        this.tableData.push({actionItem: ' 10kV I102002'})
      } else if (e == 'G') {
        this.tableData.push({actionItem: ' 10kV I102'})
      } else if (e == 'H') {
        this.tableData.push({actionItem: ' 10kV I102'})
      } else if (e == 'A') {
        this.tableData.push({actionItem: ' 10kV I102'})
      } else if (e == 'B') {
        this.tableData.push({actionItem: ' I102'})
      } else if (e == 'C') {
        this.tableData.push({actionItem: ' 10kV I102'})
      } else if (e == 'D') {
        this.tableData.push({actionItem: ' 10kV I002'})
      } else if (e == 'E') {
        this.tableData.push({actionItem: ' 10kV I002'})
      }
    },
    cancelPrve() {
      this.tableData.pop()
    },
    pdfDown() {
      window.print()
    },

    exportWordTest() {
      //
      let data = {
        list: this.tableData,
        num: this.selected,
        // 
        selectedS: this.selectedS,
        // 
        selectedOne: this.selectedOne,
        // 
        selectedTwo: this.selectedTwo
      }
      this.tableData = this.tableData.filter((item, index) => {
        return (item.index = index + 1)
      })
      exportWord('demo.docx', data, '')
    }
  }
}
</script>

<style>
/*   cssprint */
@media print {
  .no-print {
    display: none;
  }
}

/**/
@page {
  margin: 60px 10px;
}

.mains {
  padding: 10px;
  font-size: 25px;
}

.operation {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 10px;
}

.operations {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 70px 10px;
}

.leftButton_top {
  height: 200px;
}

.leftButton {
  padding-bottom: 100px;
  padding-left: 75px;
}

.leftButton_bottom {
  padding-left: 75px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

.leftButton_one {
  width: 50px;
  height: 50px;
  background-color: blue;
}

.leftButton_two {
  width: 50px;
  height: 50px;
  background-color: blue;
}

.leftButton_three {
  width: 50px;
  height: 50px;
  background-color: blue;
}

.leftButton_four {
  width: 50px;
  height: 50px;
  background-color: blue;
}

.leftButton ul {
  width: 500px;
  display: flex;
  justify-content: space-evenly;
}

.leftButton ul li {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  width: 50px;
  height: 50px;
  background-color: blue;
  border-radius: 50%;
}

.el-row {
  margin-bottom: 20px;
  /* &:last-child {
      margin-bottom: 0;
    } */
}

.el-col {
  border-radius: 4px;
}

.bg-purple-dark {
  background: #99a9bf;
  /* background: #99a9bf; */
}

.bg-purple1 {
  /* background: #d3dce6; */
  background: url('../assets/122d02043b71fd79a6855cd943ce80e.jpg') no-repeat 100%;
  height: 500px;
}

.bg-purple-light {
  background: #e5e9f2;
}

.grid-content {
  border-radius: 4px;
  min-height: 36px;
  margin-right: 10px;
}

.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>
